import { React, useState, useEffect } from 'react'
import { Button, Card } from 'semantic-ui-react'
import AlertModal from '../Modals/AlertModal'
import SuccessModal from '../Modals/SuccessModal'
import './SessionCard.scss'
import config from '../../config.json'

const tokens = require('../../promises/tokens')
const attendances = require('../../promises/attendances')


const server_url = 'http://' + config.host + ':' + config.data_port + '/'

export default function SessionCard(props) {
  const { student, session, setShowModalRut } = props
  const [availablesTokens, setAvailablesTokens] = useState([])
  const [availablesTokensCount, setAvailablesTokensCount] = useState(0)
  const [showNotTokensModal, setShowNotTokensModal] = useState(false)
  const [showSuccessModal, setShowSuccessModal] = useState(false)
  const [alertText, setAlertText] = useState('')
  const [alertHeader, setAlertHeader] = useState('')

  useEffect(() => {
    tokens.findAllAvailablesByStudentAndLesson(student.id, session.lesson_id)
      .then(res => {
        if (res === null) {
          setAvailablesTokensCount = 0
        } else {
          setAvailablesTokens(res)
          setAvailablesTokensCount(res.length)
        }

      })
      .catch(err => { console.log(err) })

  }, [])

  useEffect(() => {
    if (showSuccessModal === true) {
      //setTimeout(() => setShowModalRut(false), 4000)
      
      setTimeout(() => window.location.replace(server_url), 3500)
      
    }
  }, [showSuccessModal])

  const attendance = () => {
    if (availablesTokensCount === 0) {
      setAlertText('No tienes créditos disponibles para el taller ' + session.Lesson.name + '.')
      setAlertHeader('Sin créditos')
      setShowNotTokensModal(true)
    } else {

      attendances.findOnebySessionAndStudent(session.id, student.id)
        .then(res => {
          if (res === null) {
            attendances.create(session.lesson_id, student.id, session.id, availablesTokens[0].id)
              .then(res => {
                tokens.updateStateToUsed(res.token_id)
                  .then(() => {
                    setShowSuccessModal(true)
                  })
                  .catch(err => { console.log(err) })
              })
              .catch(err => { console.log(err) })
          } else {
            setAlertText('Ya utilizaste un crédito para el taller ' + session.Lesson.name + ' y quedaste "presente" en la lista de asistencia.')
            setAlertHeader('Ingreso previo')
            setShowNotTokensModal(true)
          }
        })
        .catch(err => { console.log(err) })
    }
  }

  return (
    <>
      <Card className='session-card'>
        <Card.Header>{session.Lesson.name}</Card.Header>
        <Card.Meta>Créditos disponibles: {availablesTokensCount}</Card.Meta>
        <Card.Meta>hora de inicio {session.start.slice(0, 5)}hrs.</Card.Meta>
        <Card.Meta>Sala {session.Room.name}</Card.Meta>
        <Card.Content extra >
          <Button primary onClick={attendance}>Ingresar</Button>
        </Card.Content>
      </Card>

      <AlertModal
        show={showNotTokensModal}
        setShow={setShowNotTokensModal}
        title={alertHeader}
        size='tiny'
      >
        <p>{alertText}</p>
      </AlertModal>
      <SuccessModal
        show={showSuccessModal}
        setShow={setShowSuccessModal}
        size='small'
      >
        <div className='success-modal'>
          <label>Ingresaste al taller {session.Lesson.name}, de las {session.start.slice(0, 5)}hrs.</label>
          <br></br>
          <label>Gracias.</label>
        </div>

      </SuccessModal>
    </>

  )
}
