import { React, useEffect, useState } from 'react'
import { Item, Icon, Label, List, Grid } from 'semantic-ui-react'
import SessionCard from '../SessionCard'
import './StudenCard.scss'
import { moment } from '../../utils'
import 'moment/locale/es'

var today = moment().format('YYYY-MM-DD')

const lessonslists = require('../../promises/lessonslists')
const tokens = require('../../promises/tokens')
const sessions = require('../../promises/sessions')



export default function StudentCard(props) {
    const { student, setShowModalRut } = props
    const [studentLessonsList, setStudentLessonsList] = useState([])
    const [studentTokens, setStudentTokens] = useState([])
    const [sessionsToday, setSessionsToday] = useState([])

    useEffect(() => {
        lessonslists.findAllByStudent(student.id)
            .then(lessonslist => {
                setStudentLessonsList(lessonslist)
                tokens.findAllAvailablesByStudent(student.id)
                    .then(studentsTokensData => {
                        setStudentTokens(studentsTokensData)
                        var sessionsArray = []
                        var promisesList = []
                        lessonslist.forEach(lessonListRes => {
                            promisesList.push(
                                sessions.findAllByLessonAndDate(lessonListRes.lesson_id, today)
                                    .then(sessionsRes => {
                                        if (sessionsRes.length !== 0) {
                                            sessionsRes.forEach(item => {
                                                sessionsArray.push(item)
                                            })
                                        }
                                    })
                                    .catch(err => { console.log(err) })
                            )
                        });
                        Promise.all(promisesList)
                        .then(() => {
                            setSessionsToday(sessionsArray)
                        })
                        .catch(err => console.log(err))


                    })
                    .catch(err => { console.log(err) })
            })
            .catch(err => { console.log(err) })

    }, [])


    return (
        <div>
            <Grid columns={2} divided>
                <Grid.Row>
                    <Grid.Column>
                        <Item>
                            <Item.Content>
                                <Item.Header as={'h2'}> <Icon name='user' />{student.name}</Item.Header>
                                <Item.Meta><Icon name='phone' />{student.phone}</Item.Meta>
                                <Item.Meta><Icon name='mail' />{student.mail}</Item.Meta>
                                <Item.Meta><Icon name='home' />{student.address}</Item.Meta>
                            </Item.Content>
                        </Item>
                    </Grid.Column>
                    <Grid.Column>
                        <Item className='class-list'>
                            <Item.Content>
                                <Item.Header as={'h3'}>{'Créditos disponibles totales: ' + studentTokens.length} </Item.Header>
                            </Item.Content>
                        </Item>
                        <Item className='class-list'>
                            <Item.Content>
                                <Item.Header as={'h3'}>Talleres inscritos: </Item.Header>
                                <List horizontal relaxed>
                                    {
                                        studentLessonsList.map(item =>
                                            <List.Item key={item.id} className='lesson'>
                                                <Label >{item.Lesson.name}</Label>
                                            </List.Item>
                                        )
                                    }
                                </List>
                            </Item.Content>
                        </Item>
                    </Grid.Column>
                </Grid.Row>
            </Grid>

            <div className='sessions'>
                <Item className='session-list'>
                    <Item.Content>
                        <Item.Header as={'h3'}>Tus Talleres de hoy: </Item.Header>
                        <List horizontal relaxed>
                            {
                                sessionsToday.map(item =>
                                    //console.log(item.id)
                                    <List.Item key={item.id} className='session'>
                                        <SessionCard student={student} session={item} setShowModalRut={setShowModalRut}/>
                                    </List.Item>
                                )
                            }
                        </List>
                    </Item.Content>
                </Item>
            </div>
        </div>
    )
}


