import { React, useState, useRef, useEffect } from 'react'
import { Button, Form, Grid, Input } from 'semantic-ui-react'
import BasicModal from '../Modals/BasicModal/BasicModal';
import StudentCard from '../StudentCard';
import './RutForm.scss'
//const utils = require('../../utils')

const students = require('../../promises/students')




export default function RutForm() {
    const [showModalRut, setShowModalRut] = useState(false)
    const [rutData, setRutData] = useState(rutDataDefault())
    const [findRutResult, setFindRutResult] = useState('')
    const [student, setStudent] = useState([])
    const rutRef = useRef()

    useEffect(() => {
        setRutData({
            rut: ''
        })
        rutRef.current.value = ''
        rutRef.current.focus()
        setFindRutResult('')
        // rutRef.current.innerHTML = ''
        // console.log('uhuhuhs')
    }, [showModalRut])


    const handleRutOnChange = (e, { value }) => {

        //e.target.value = utils.scanRut(value)
        //e.target.value = value.search(/[RUN¡][0-9][0-9][0-9][0-9][0-9][0-9][0-9][0-9]['][A-Z])\w+/)
        handleOnChange(e)
    }

    const handleOnChange = (e) => {
        setRutData({
            // ...rutData,
            [e.target.name]: e.target.value
        })
    }

    const rutModal = () => {
        let scanRut = rutData.rut
        let scanResult = scanRut.match(/(RUN[¡][0-9]{7,8}['][A-Z 0-9 a-z])/g)
        let scanString = scanResult.toString()
        let scanRuttoFind = ''
        //¡23050962'K
        //RUN¡13050962'8
        let checkDegit = scanString.slice(-1)
        let group3 = scanString.slice(-5, -2)
        let group2 = scanString.slice(-8, -5)
        let group1 = ''
        if (scanString.length === 13) {
            group1 = scanString.slice(-9, -8)
        } else if (scanString.length === 14) {
            group1 = scanString.slice(-10, -8)
        }
        scanRuttoFind = group1 + '.' + group2 + '.' + group3 + '-' + checkDegit

        rutRef.current.value = ''
        students.findOnebyRut(scanRuttoFind)
            .then(res => {
                if (res === null) {
                    setFindRutResult('Rut No encontrado')
                    setRutData({
                        rut: ''
                    })
                    rutRef.current.value = ''
                } else {
                    setStudent(res)
                    setShowModalRut(true)
                }

                // console.log(res)

            })
            .catch(err => {
                console.log(err)
                setFindRutResult('Error de Conexión con el Servidor.')
                setRutData({
                    rut: ''
                })
                rutRef.current.value = ''
                rutRef.current.focus()
            })
    }


    return (
        <>
            <Grid className='grid-form'>
                <Grid.Row className='row-form'>
                    <Grid.Column>
                        <Form onSubmit={rutModal} className='form'>
                            <Form.Field inline>
                                {/* <label className='form-label'>Rut</label> */}
                                <Input
                                    //autocomplete="off"
                                    name='rut'
                                    onChange={handleRutOnChange}
                                    // className='form-input'
                                    value={rutData.rut}
                                    ref={rutRef}
                                    placeholder='Escanear Cédula'
                                    required />
                            </Form.Field>
                            <Form.Field>
                                <Button role='submit' className='btn-in'>
                                    ingresar
                                </Button>
                            </Form.Field>
                        </Form>
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row>
                    <Grid.Column>
                        <h3>{findRutResult}</h3>
                    </Grid.Column>
                </Grid.Row>
            </Grid>

            <BasicModal
                show={showModalRut}
                setShow={setShowModalRut}
                title='Bienvenido'
                size='small'
            >
                <StudentCard student={student} setShowModalRut={setShowModalRut} />
            </BasicModal>

        </>

    )
}

function rutDataDefault() {
    return ({
        rut: '',
    })
}