
import config from '../config.json'
const server_url = 'http://'+config.host+':'+config.data_port+'/'



function findAll(){
    const students = new Promise((resolve, reject) => {
        fetch(server_url + 'students/findAll', {
            method: 'GET',
            body: JSON.stringify(),
            headers: { 'Content-Type': 'application/json'}
        }).then(res => {
            //console.log(config.password)
            res.json().then(res => {
                if (res.code === 0) {
                    reject(res.data)
                } else {
                    resolve(res.data)
                }
            })
        }).catch(err => {console.log(err)})
    })
    return students
}
function destroy(id) {
    let data = {'id':id}
    const student = new Promise((resolve, reject) => {
        fetch(server_url + 'students/destroy', {
            method: 'POST',
            body: JSON.stringify(data),
            headers: { 'Content-Type': 'application/json' }
        }).then(res => {
            res.json().then(res => {
                if (res.code === 0) {
                    reject(res.data)
                } else {
                    resolve(res.data)
                }
            })
        }).catch(err => {console.log(err)})
    })

    return student
}

