import React from 'react'
import { Modal, Button, Grid } from 'semantic-ui-react'
import { moment } from '../../../utils'
import 'moment/locale/es'

var today = moment()


// size: 'mini'
// size: 'tiny' 
// size: 'small'
// size: 'large'
// size: 'fullscreen'

export default function BasicModal(props) {
    const { show, setShow, title, size, children} = props

    const onClose = () => {
        setShow(false)
    }
    return (
        <Modal className='basic-modal' open={show} onClose={onClose} size={size} dimmer= 'blurring' closeOnDimmerClick={false}>
            <Modal.Header>
                <Grid columns={2}>
                    <Grid.Column width={13}>
                    <h3>{title}</h3>
                    </Grid.Column>
                    <Grid.Column width={3}>
                        <h3>{today.format('DD-MM-YYYY')}</h3>
                    </Grid.Column>
                </Grid>
            </Modal.Header>
            <Modal.Content>{children}</Modal.Content>
            <Modal.Actions>
                <Button onClick={onClose}>
                    Cerrar
                </Button>
            </Modal.Actions>

        </Modal>
    )
}
