import React from 'react'
import { Modal, Button } from 'semantic-ui-react'
import './AlertModal.scss'


// size: 'mini'
// size: 'tiny' 
// size: 'small'
// size: 'large'
// size: 'fullscreen'



export default function AlertModal(props) {
    const { show, setShow, title, size, children} = props

    const onClose = () => {
        setShow(false)
    }
    return (
        <Modal className='alert-modal' open={show} onClose={onClose} size={size} dimmer= 'blurring' closeOnDimmerClick={false}>
            <Modal.Header className='alert'>
                <h3>{title}</h3>
            </Modal.Header>
            <Modal.Content className='alert'>{children}</Modal.Content>
            <Modal.Actions>
                <Button onClick={onClose}>
                    Cerrar
                </Button>
            </Modal.Actions>

        </Modal>
    )
}

